<template>
	<el-row :gutter="20" class="container">
	  <el-col :span="24">
		  	<div class="login-icon">
					<el-button @click="checkCookie()">set cookie</el-button>
		  		<img  src="../assets/imgs/login.jpg">
		  	</div>
	  </el-col>
	</el-row>
</template>
<script>
import {Base64} from '@/assets/js/base64.js'

export default {
  name: 'App',
    data() {
      return {}
    },
	methods: {
		uuid() {
			var s = [];
			var hexDigits = "0123456789abcdef";
			for (var i = 0; i < 36; i++) {
				s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
			}
			s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
			s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
			s[8] = s[13] = s[18] = s[23] = "-";

			var uuid = s.join("");
			return uuid;
		},
		//检查客户端cookie
		checkClientCookie() {
				let client_id = this.Cookie.getCookie('client_id');
				if (client_id != null && client_id != "") return false;
				this.Cookie.setCookie('client_id', 'web', 14) 
		},
		checkInstallCookie(){
			let installation_ID = this.Cookie.getCookie('installation_ID')				
			if (installation_ID != null && installation_ID != "") {
				//alert('你的installation_ID ' + installation_ID + '!')
			} else {
				installation_ID = this.uuid() + '-' + new Date().getTime()
				if (installation_ID != null && installation_ID != "") {
							let device_info = Base64.toBase64(JSON.stringify({
													deviceType: 'PC', 
													osType: 'windows', 
													agentType: 'Browser', 
													hardwareUid: {
															PC:{
																disk_id: '',
																mac_addresses:'' 
															}
													}        
												}));
							this.$store.dispatch('postInstalltionId', {installationId: installation_ID, device_info: device_info, client_id: this.Cookie.getCookie('client_id')}).then(response => {
							this.Cookie.setCookie('installation_ID', installation_ID, 14) // 假设后台操作成功，拿到 installation_ID
						}).catch(err => {
							alert('添加失败：' + err.data.data);
						});
				}
			}
		}
	},
    created() {
		this.checkClientCookie();
		this.checkInstallCookie();
		let access_token = this.Cookie.getCookie("access_token");
		if(!access_token) {
			if(!!this.$route.params.data) {
				//请求token接口
				this.Cookie.setCookie('access_token', 'xxxx2');
				this.$emit("viewIn");
			}
		}
	},
	updated() {}
}
</script>



